.toolbar.ant-layout-header {
    padding: 0 0 0 20px;
    color: white;
    height: 48px;
    line-height: 48px;
}

.toolbar__item {
    padding-right: 15px;
}

.toolbar__item .anticon {
    margin-right: 4px;
}

.btn-margin {
    margin-left: 10px;
}
.graph_area {
    position: relative;
    background-color: #fff;
}

.graph {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

.cytoscape_container {
    width: 100%;
    height: 100%;
}

.editor__sidebar {
    background-color: rgb(240, 242, 245) !important;
    padding: 24px;
}

.wrapper{
    display:flex;
    flex-direction: column;
}

.parent {
    white-space: nowrap;
}

.inline-right {
    display: inline-block;
    float: right;
}

.inline-left {
    display: inline-block;
    float: left;
}

.center {
    margin: 0 auto;
}

.logo {
    height: 39px;
    width: 100%;
    background-repeat: no-repeat;
    background-position-x: center;
    background-image: url(data:image/png;base64,);
    margin-top: 20px;
}

.create_box {
    padding: 4px 4px;
}

.create_box .ant-input-affix-wrapper .ant-input-suffix {
    right: 0 !important;
}

.trace-item {
    font-size: 1.20rem;
}

.trace-item button {
    width: 32px;
    height: 25px;
}

.trace-item-number {
    width: 134px;
    height: 25px;
}

.parent { white-space: nowrap; }

.inline-right { display: inline-block; float: right; }

.inline-left { display: inline-block; float: left; }



.case-header {
    color: whitesmoke;
    margin-top: 15px;
    margin-bottom: 21px;
    margin-left: 38px;
    font-size: 23px;
}

.mode_menu.ant-menu-horizontal {
    line-height: 64px;
}

.ant-menu.ant-menu-dark .mode_menu__process_model.ant-menu-item-selected {
    background-color: #008B27;
}

.ant-menu.ant-menu-dark .mode_menu__run_algorithm.ant-menu-item-selected {
    background-color: #008B27;
}

.topbar.ant-layout-header {
    padding: 0;
}

.outer_layout {
    min-height: 100vh;
}

body {
    margin: 0;
    padding: 0;
}
